export const CheckValue = (type, input) => {
  const isNumber = (value) => {
    return !isNaN(Number(value));
  };

  const isError = (value) => {
    if (
      value === "Syntax Error" ||
      value === "Unreal Answer" ||
      value === "Out of Range"
    ) {
      return value;
    }
    return false;
  };

  const constants = ["ùëí", "œÄ"];
  const isConstant = (val) => {
    let flag = false;
    constants.forEach((value) => {
      if (value === val) {
        flag = true;
      }
    });
    return flag;
  };

  const exponents = [
    "^",
    "¬≤",
    "¬≥",
    "‚àö¬≤",
    "‚àö¬≥",
    "log‚ÇÇ",
    "log‚ÇÅ‚ÇÄ",
    "log‚Çë",
    "!",
    "sin",
    "cos",
    "tan",
    "sinh",
    "cosh",
    "tanh",
    "sin‚Åª¬π",
    "cos‚Åª¬π",
    "tan‚Åª¬π",
    "sinh‚Åª¬π",
    "cosh‚Åª¬π",
    "tanh‚Åª¬π",
    "¬∞",
    "rad",
  ];
  const isExpression = (val) => {
    let flag = false;
    exponents.forEach((value) => {
      if (value === val) {
        flag = true;
      }
    });
    return flag;
  };
  const isLeftSideExpression = (val) => {
    return (
      isExpression(val) &&
      val !== "!" &&
      val !== "^" &&
      val !== "¬≤" &&
      val !== "¬≥" &&
      val !== "¬∞"
    );
  };
  const isRightSideExpression = (val) => {
    return val === "!" || val === "¬≤" || val === "¬≥" || val === "¬∞";
  };
  const isArithmetic = (value) => {
    let flag = false;
    if (value === "√ó" || value === "√∑" || value === "+" || value === "-") {
      flag = true;
    }
    return flag;
  };

  switch (type) {
    case "Error":
      return isError(input);
    case "Number":
      return isNumber(input);
    case "Constant":
      return isConstant(input);
    case "Expression":
      return isExpression(input);
    case "LeftSideExpression":
      return isLeftSideExpression(input);
    case "RightSideExpression":
      return isRightSideExpression(input);
    case "Arithmetic":
      return isArithmetic(input);
    default:
      return false;
  }
};
